.form-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #fff;
  padding: 3rem 0;
}

label {
  margin-right: 10px;
  font-size: 20px;
}

.form-item {
  max-width: 250px;
  position: relative;
  margin-bottom: 3rem;
}

.form-input {
  padding: 5px;
}

.form-input:not(input[type='checkbox']) {
  width: 240px;
}

.agreed {
  flex-direction: row;
  margin-bottom: 4rem;
}

.agreed .err-msg {
  top: 30px;
}
