.toggle-button {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 25px;
  margin: 0;
  vertical-align: top;
  background: #ffffff;
  border: 1px solid #bbc1e1;
  border-radius: 30px;
  outline: none;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  transition: all 0.3s cubic-bezier(0.2, 0.85, 0.32, 1.2);
}

.toggle-button::after {
  content: '';

  display: inline-block;
  position: absolute;
  left: 3px;
  top: 2px;

  width: 20px;
  height: 20px;
  background-color: rgb(87, 87, 100);
  border-radius: 100%;

  transform: translateX(0);
  transition: all 0.3s cubic-bezier(0.2, 0.85, 0.32, 1.2);
}

.toggle-button:checked::after {
  transform: translateX(calc(100% + 13px));
  background-color: #fff;
}

.toggle-button:checked {
  background-color: rgb(87, 87, 100);
}
